import "./style.css";
import React, { Component } from "react";
import HeaderMenu from "../HeaderMenu"
import LogoIcon from "../LogoIcon"

class Header extends Component {

  constructor(props){
    super(props);
    this.onClickMenuIcon = this.onClickMenuIcon.bind(this);
  }

  onClickMenuIcon() {
    if(document.querySelector("header nav").classList.contains("open")) {
        document.querySelector("header nav").classList.remove("open");
    } else {
      document.querySelector("header nav").classList.add("open");
    }
  }

  render() {
    return(
      <header id="header" className="centerElements">
        <div className="centerContainer">
          <LogoIcon />
          <HeaderMenu className="menu" />
          <button className="menuIcon" onClick={() => this.onClickMenuIcon()}>
            <i class="fas fa-bars"></i>
          </button>
        </div>
      </header>
    );
  }

}

export default Header;
