import React, { Component } from "react";
import "./style.css";

class HeaderMenu extends Component {

  constructor(props){
    super(props);
    this.onClickMenu = this.onClickMenu.bind(this);
    this.onClickContact = this.onClickContact.bind(this);
  }

  onClickMenu(elementID, elementToScroll) {
    document.querySelectorAll("header nav a, header nav button").forEach(function(element) {
      element.classList.remove("active")
    });

    document.getElementById(elementID).classList.add("active");
    document.querySelector("header nav").classList.remove("open");

    var topOfElement = document.getElementById(elementToScroll).offsetTop - 120;

    window.scroll({
      top: topOfElement,
      behavior: "smooth"
    });
  }

  onClickContact() {
    document.querySelector("header nav").classList.remove("open");
  }


  render() {
    return(
      <div>
        <nav>
          <button type="button" onClick={() => this.onClickMenu("menuAbout", "aboutMe")} id="menuAbout" className="active">Sobre</button>
          <button type="button" onClick={() => this.onClickMenu("menuWorks", "portifolio")} id="menuWorks">Trabalhos</button>
          <button type="button" onClick={() => this.onClickMenu("menuCustomers", "customerTestimonials")} id="menuCustomers">Clientes</button>
          <a onClick={() => this.onClickContact()} href="https://www.workana.com/freelancer/314366ae75457706c27e8fd69ef2ec5b" rel="noopener noreferrer" target="_blank" id="menuContact">Contato</a>
        </nav>
      </div>
    );
  }

}

export default HeaderMenu;
