import "./style.css";
import React from "react";

const PortifolioItem = (props) => (

  <div class="portifolioItem">
    <div className="portifolioItemImage">
      <img src={props.image} alt={props.title} />
    </div>
    <div className="portifolioItemDescription">
      <div className="portifolioItemDescriptionText">
        <h4>{props.title}</h4>
        <p>{props.tecnology}</p>
        {props.link != null && <a href={props.link} target="_blank" rel="noopener noreferrer">Visualizar projeto</a> }
      </div>
    </div>
  </div>

);

export default PortifolioItem;
